<template>
  <div class="container">
    <div class="row">
      <div class="col-12">

        <form method="get" class="cmxform" id="form1" action="">
          <fieldset>
            <legend></legend>
            <p>
              <label for="user"></label>
              <input id="user" name="user" required minlength="3">
            </p>
            <p>
              <label for="password"></label>
              <input id="password" type="password" maxlength="12" name="password" required minlength="5">
            </p>
            <p>
              <input class="submit" type="submit" value="Login">
            </p>
          </fieldset>
        </form>

      </div>
    </div>
  </div>
</template>
<script>
import 'jquery-validation'
export default {
  name: 'jqueryvalidation4',
  created () { },
  mounted () {
    // 
    $.validator.addMethod('alphabetnumber', function (value, element) {
      var re = /^[a-zA-Z0-9]+$/g

      return value.match(re)
    })

    // 
    var validator = $('#form1').validate({
      errorPlacement: function (error, element) {
        // Append error within linked label
        $(element)
          .closest('form')
          .find("label[for='" + element.attr('id') + "']")
          .append(error)
      },
      errorElement: 'span',
      rules: {
        password: {
          required: true,
          minlength: 4,
          maxlength: 20,
          alphabetnumber: true
        }
      },
      messages: {
        user: {
          required: ' ()',
          minlength: ' ( 3 )'
        },
        password: {
          required: '.',
          minlength: '4.',
          maxlength: '20',
          alphabetnumber: ''
        }
      }
    })

    $('.cancel').click(function () {
      validator.resetForm()
    })
  }
}
</script>



