        <p class="news_content"></p>
      </li>
      <li>
        <h2></h2>
        <p class="news_content"></p>
      </li>
      <li>
        <h2></h2>
        <p class="news_content"></p>
      </li>
      <li>
        <h2></h2>
        <p class="news_content"></p>
