    </div>
  </div>
</template>
<script>
export default {
  name: 'typesetting3'
}
</script>
<style lang="css" scoped>
* {
  box-sizing: border-box;
}

.wrap {
  width: 100%;
  max-width: 1080px;
  margin-top: 20px;
  margin: 0 auto;
  min-height: 100%;
}

.news {
  list-style: none;
  width: 100%;
  max-width: 1080px;
  margin: 0 auto;
  padding: 0;
}

.news:after {
  content: ' ';
  display: block;
  clear: both;
}

.news li {
  width: 24%;
  float: left;
  /* important */
  /* display: inline-block; */
  margin: 0 0.5%;
  transition: width 0.3s linear;
}

.news h2 {
  font-weight: 900;
  text-align: left;
  letter-spacing: 3px;
}

.news p {
  line-height: 1.8;
  letter-spacing: 3px;
  text-align: left;
}

.main {
  width: 100%;
  /* border: 1px solid red; */
  background-color: #eee;
}

@media screen and (max-width: 768px) {
  .news li {
    width: 49%;
  }
}

@media screen and (max-width: 480px) {
  .news li {
    width: 100%;
  }
}
</style>



