<template>
  <div>
    <div class="myContainer">
      <header>
        <h1 class="logo">
          <a href="#">LOGO</a>
        </h1>
        <ul class="menu">
          <li>
            <a href="#"></a>
          </li>
          <li>
            <a href="#"></a>
          </li>
          <li>
            <a href="#"></a>
          </li>
          <li>
            <a href="#"></a>
          </li>
          <li>
            <a href="#"></a>
          </li>
        </ul>
      </header>
      <section>
        <div class="content">
          <h2></h2>
          <p> (2016-06-21 09:06)</p>
          <p></p>
          <p></p>
        </div>
        <div class="sidebar">
          <img src="http://fakeimg.pl/300x225/" alt="">
          <img src="http://fakeimg.pl/300x225/" alt="">
          <img src="http://fakeimg.pl/300x225/" alt="">
        </div>
      </section>
      <footer>Copyright 2016</footer>
    </div>
  </div>
</template>
<script>
export default {
  name: 'typesetting6'
}
</script>
<style lang="css" scoped>
* {
  box-sizing: border-box;
}

.myContainer {
  width: 100%;
  max-width: 1080px;
  margin: 0 auto;
  margin-top: 100px;
}

header {
  height: 350px;
  width: 100%;
  position: relative;
  background-color: rgba(255, 0, 0, 0.063);
}

header:after{
  content: '';
  display: block;
  clear: both;
}

header .logo {
  position: absolute;
  left: 0px;
  top: 30px;

  border: 1px solid red;
}

header .logo a {
  width: 250px;
  max-width: 100%;
  display: block;
  height: 250px;
  background: url('http://fakeimg.pl/250x250/') no-repeat;
  text-indent: -9999px;
}

header .menu {
  float: right;
  padding: 0;
  margin: 0;
  margin-top: 30px;
  border: 1px solid green;
}

header .menu:after{
  content: '';
  display: block;
  clear: both;
}

header .menu li {
  list-style: none;
  width: 100px;
  float: left;
  display: block;
  background-color: #3399ff;
  border-right: 2px solid white;
}

header .menu a {
  color: white;
  display: block;
  font-weight: bold;
  text-align: center;
  height: 30px;
  line-height: 30px;
  text-decoration: none;
}

section {
  margin-top: 20px;
  /* height: calc(100% - 350px -23px); */
  background-color: rgba(22, 255, 5, 0.083);
}

section:after {
  content: '';
  display: block;
  clear: both;
}

section .content {
  float: left;
  text-align: left;
  width: 75%;
  padding: 15px;
  border: 2px solid #636363;
  border-radius: 5px;
}

section .content h2 {
  font-weight: bold;
  font-size: 26px;
  padding: 30px 0 20px 0;
}

section .content p {
  letter-spacing: 1pt;
  line-height: 1.8;

  padding-bottom: 30px;
}

section .sidebar {
  float: left;
  width: 20%;
  margin-left: 5%;
  border: 2px solid #636363;
  border-radius: 5px;
  padding: 5px;
}

section .sidebar img {
  margin-bottom: 10px;
  display: block;
  max-width: 100%;
}

footer {
  margin-top: 20px;
      height: 35px;
    line-height: 35px;
  width: 100%;
  background-color: rgba(65, 105, 225, 0.131);
  padding: 5px auto;
  color: #707070;
  text-align: center;
}

@media screen and (max-width: 768px) {

header{
  height: auto;
}
  
  header .logo {
    position: relative;
    display: block;
    margin: 0 auto;
    text-align: center;
    width: 150px;
  }

  header .logo a {
    width: 150px;
    height: 170px;
    background: url('http://fakeimg.pl/150x150/') no-repeat;
  }

  header .menu {
    float: none;
  }

  header .menu li {
    width: 92%;
    margin-bottom: 10px;
    margin-left: 4%;
    margin-right: 4%;
  }


  section .content{
    float: none;
    width: 100%;
    display: block;
  }

  section .sidebar{
    float: none;
    margin: 0 auto;
    margin-top: 20px;
  }

}
</style>



