<template>
  <div class="container">
    <div class="row">
      <div class="row">

        <select id="sel_menu" class="js-example-templating js-states form-control">
          <optgroup label="">
            <option value="1"></option>
            <option value="2"></option>
            <option value="3"></option>
            <option value="4"></option>
          </optgroup>
          <optgroup label="">
            <option value="5"></option>
            <option value="6"></option>
            <option value="7"></option>
            <option value="8"></option>
          </optgroup>
          <optgroup label="">
            <option value="9"></option>
          </optgroup>
        </select>

      </div>
    </div>
  </div>
</template>
<script>
import 'select2/dist/js/select2.min.js'
export default {
  name: 'select23',
  created () { },
  mounted () {
    // 
    $('#sel_menu').select2({
      templateResult: formatState,
      templateSelection: formatState
    })

    function formatState (state) {
      if (!state.id) { return state.text }
      // 
      // var $state = $(
      //   '<span><img src="/content/images/' + state.element.value.toLowerCase() + '.ico" class="img-flag" /> ' + state.text + '</span>'
      // )
      // 
      var $state = $(
        '<span><img src="http://fakeimg.pl/20x20/" class="img-flag" /> ' + state.text + '</span>'
      )
      return $state
    };
  },
  methods: {}
}
</script>

<style lang="css">

</style>
<style lang="css">
@import 'select2/dist/css/select2.min.css';
</style>



