      window.paceOptions = {
        restartOnPushState: true,
        ajax: true,
        document: true,
        restartOnRequestAfter: true
      }

      // $.mockjax({
      //   url: '/products/aaaa',
      //   responseTime: 3750,
      //   response: function(settings) {
      //     return 'You want a cook book!'
      //   }
      // })

      //  Pace.stop();
      // $.ajax({
      //   url: '/products/aaaa',
      //   type: 'get',
      //   error: function(xhr) {
      //     alert('Ajax request ')
      //   },
      //   success: function(response) {
      //     console.log('')
      //     // Pace.restart()
      //     // Pace.start();
      //   }
      // })
    })()
  }
}
</script>
<style lang="css" scoped>
/* PaceJS */
.pace {
  -webkit-pointer-events: none;
  pointer-events: none;

  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}

.pace-inactive {
  display: none;
}

.pace .pace-progress {
  background: #ff006b;
  position: fixed;
  z-index: 2000;
  top: 0;
  right: 100%;
  width: 100%;
  height: 0.35em;
}
</style>



