<template>
  <div class="container">
    <div class="row">
      <div class="col-12">

        <form class="cmxform" id="signupForm" method="get" action="">
          <fieldset>
            <legend></legend>
            <p>
              <label for="firstname"></label>
              <input id="firstname" name="firstname" type="text" value="123">
            </p>
            <p>
              <label for="lastname"></label>
              <input id="lastname" name="lastname" type="text" value="123">
            </p>
            <p>
              <label for="username"></label>
              <input id="username" name="username" type="text" value="123">
            </p>
            <p>
              <label for="password"></label>
              <input id="password" name="password" type="password" value="1234566">
            </p>
            <p>
              <label for="confirm_password"></label>
              <input id="confirm_password" name="confirm_password" type="password" value="1234566">
            </p>
            <p>
              <label for="email">Email</label>
              <input id="email" name="email" type="email" value="as@gma">
            </p>
            <p>
              <label for="agree"></label>
              <input type="checkbox" class="checkbox" id="agree" name="agree" checked="true">
            </p>
            <p>
              <label for="newsletter"></label>
              <input type="checkbox" class="checkbox" id="newsletter" name="newsletter">
            </p>
            <fieldset id="newsletter_topics">
              <legend> () - </legend>
              <label for="topic_marketflash">
                <input type="checkbox" id="topic_marketflash" value="marketflash" name="topic">Marketflash
              </label>
              <label for="topic_fuzz">
                <input type="checkbox" id="topic_fuzz" value="fuzz" name="topic">Latest fuzz
              </label>
              <label for="topic_digester">
                <input type="checkbox" id="topic_digester" value="digester" name="topic">Mailing list digester
              </label>
              <label for="topic" class="error">Please select at least two topics you'd like to receive.</label>
            </fieldset>
            <p>
              <input class="submit" type="submit" value="">
            </p>
          </fieldset>
        </form>

      </div>
    </div>
  </div>
</template>
<script>
import 'jquery-validation'
export default {
  name: 'jqueryvalidation2',
  created () { },
  mounted () {
    // $.validator.setDefaults({
    //   submitHandler: function (form) {
    //     alert('!')
    //     form.submit();
    //   }
    // })

    // 
    $('#signupForm').validate({
      rules: {
        firstname: 'required',
        lastname: 'required',
        username: {
          required: true,
          minlength: 2
        },
        password: {
          required: true,
          minlength: 5
        },
        confirm_password: {
          required: true,
          minlength: 5,
          equalTo: '#password'
        },
        email: {
          required: true,
          email: true
        },
        topic: {
          required: '#newsletter:checked',
          minlength: 2
        },
        agree: 'required'
      },
      messages: {
        firstname: '',
        lastname: '',
        username: {
          required: '',
          minlength: ''
        },
        password: {
          required: '',
          minlength: ' 5 '
        },
        confirm_password: {
          required: '',
          minlength: ' 5 ',
          equalTo: ''
        },
        email: '',
        agree: '',
        topic: ''
      },
      submitHandler: function (form) {
        alert('!')
        form.submit()
      }
    })
  }
}
</script>



