<template>
  <div class="container mt-5">
    <div class="row">
      <section class="col-12">
        <a href="https://www.oxxostudio.tw/articles/201408/css-vertical-align.html" target="_blank">
          <div class="alert alert-success" role="alert"></div>
        </a>
      </section>
      <section class="col-3 boxShadow">
        <div class="alert alert-secondary" role="alert"></div>
        <h3>
          <b>heightline-height</b>
        </h3>
        <p class="text-left">
          <b>:</b>
        </p>
        <pre class="text-left">
.redbox {
  display: inline-block;
  width: 30px;
  height: 30px;
}
        </pre>
        <div class="div0">
          <div class="redbox"></div>
          <div class="greenbox"></div>
          <div class="bluebox"></div>
        </div>
        <div class="div0">
          <div class="redbox"></div>
        </div>
      </section>
      <section class="col-3 boxShadow">
        <div class="alert alert-secondary" role="alert"></div>
        <h3>
          <b> ( ::before::after )</b>
        </h3>
        <p class="text-left">
          <b>: vertical-align</b>
        </p>
        <p class="text-left">
          <b>:<span style="color:red;">  :after:before</span> </b>
        </p>
        <p class="text-left">
          <b>: div  display 
            <span style="color:red;"> inline-block</span> vertical-align:middle; div  block</b>
        </p>
        <pre class="text-left">
.div2:before {
  content: '';
  width: 0;
  height: 100%;
  display: inline-block;
  position: relative;
  vertical-align: middle;
}
        </pre>
        <div class="div2">
          <div class="redbox2"></div>
          <div class="greenbox2"></div>
          <div class="bluebox2"></div>
        </div>
        <div class="div2">
          <div class="redbox2"></div>
        </div>
      </section>
      <section class="col-3 boxShadow">
        <div class="alert alert-secondary" role="alert"></div>
        <h3>
          <b>calc </b>
        </h3>
        <p class="text-left">
          <b>: div  display 
            <span style="color:red;"> block</span>
          </b>
        </p>
        <p class="text-left">
          <b>:50%  + 50%  div </b>
        </p>
        <pre class="text-left">
.greenbox3{
  position: relative;
  width:30px;
  height:80px;
  float:left;
  /*  */
  top:calc(50% - 40px);
}
        </pre>
        <div class="div3">
          <div class="redbox3"></div>
          <div class="greenbox3"></div>
          <div class="bluebox3"></div>
        </div>
        <div class="div3">
          <div class="redbox3"></div>
        </div>
      </section>
      <section class="col-3 boxShadow">
        <div class="alert alert-secondary" role="alert"></div>
        <h3>
          <b>transform:translateY</b>
        </h3>
        <p class="text-left">
          <b>: 
            <span style="color:red;"> position:relative</span>
          </b>
        </p>
        <p class="text-left">
          <b>: 
            <span style="color:red;"></span>
          </b>
        </p>
        <pre class="text-left">
.redbox4,
.greenbox4,
.bluebox4 {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  display: inline-block;
}

        </pre>
        <div class="div4">
          <div class="redbox4"></div>
          <div class="greenbox4"></div>
          <div class="bluebox4"></div>
        </div>
        <div class="div4">
          <div class="redbox4"></div>
        </div>
      </section>
      <section class="col-3 boxShadow">
        <div class="alert alert-secondary" role="alert"></div>
        <h3>
          <b>position: absolute;</b>
        </h3>
        <p class="text-left">
          <b>: 
            <span style="color:red;"></span>
          </b>
        </p>
        <pre class="text-left">
.redbox5,
.greenbox5,
.bluebox5 {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
        </pre>
        <div class="div5">
          <div class="redbox5"></div>
          <div class="greenbox5"></div>
          <div class="bluebox5"></div>
        </div>
        <div class="div5">
          <div class="redbox5"></div>
        </div>
      </section>
      <section class="col-3 boxShadow">
        <div class="alert alert-secondary" role="alert"></div>
        <h3>
          <b>display:flex;</b>
        </h3>
        <pre class="text-left">
.div6 {
  width: 100%;
  height: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
}
        </pre>
        <div class="div6">
          <div class="redbox6"></div>
          <div class="greenbox6"></div>
          <div class="bluebox6"></div>
        </div>
        <div class="div6">
          <div class="redbox6"></div>
        </div>
      </section>
    </div>
  </div>
</template>
<script>
export default {
  name: 'typesetting7'
}
</script>
<style lang="css" scoped>
.boxShadow {
  box-shadow: 0 0 20px #e3e3e3;
}

/*  */
.div0 {
  width: 100%;
  height: 150px;
  line-height: 150px;
  border: 1px solid #000;
  text-align: center;
}
.redbox {
  display: inline-block;
  width: 30px;
  height: 30px;
  background: #c00;
}
.greenbox {
  width: 30px;
  height: 80px;
  background: #0c0;
  display: inline-block;
  vertical-align: middle;
}
.bluebox {
  width: 30px;
  height: 40px;
  background: #00f;
  display: inline-block;
  vertical-align: middle;
}

/*  */
.div2 {
  width: 100%;
  height: 150px;
  border: 1px solid #000;
  text-align: center;
}

.div2:before {
  content: '';
  width: 0;
  height: 100%;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  background-color: #f00;
}

.redbox2 {
  width: 30px;
  height: 30px;
  background: #c00;
  display: inline-block;
  vertical-align: middle;
}
.greenbox2 {
  width: 30px;
  height: 60px;
  background: #0c0;
  display: inline-block;
  vertical-align: middle;
}
.bluebox2 {
  width: 30px;
  height: 40px;
  background: #00f;
  display: inline-block;
  vertical-align: middle;
}

/*  */
.div3 {
  width: 100%;
  height: 150px;
  border: 1px solid #000;
}
.redbox3 {
  position: relative;
  width: 30px;
  height: 30px;
  background: #c00;
  float: left;
  /*  */
  top: calc(50% - 15px);
  margin-left: calc(50% - 45px);
}
.greenbox3 {
  position: relative;
  width: 30px;
  height: 80px;
  background: #0c0;
  float: left;
  /*  */
  top: calc(50% - 40px);
}
.bluebox3 {
  position: relative;
  width: 30px;
  height: 40px;
  background: #00f;
  float: left;
  /*  */
  top: calc(50% - 20px);
}

/*  */
.div4 {
  width: 100%;
  height: 150px;
  border: 1px solid #000;
}

.redbox4,
.greenbox4,
.bluebox4 {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  display: inline-block;
}

.redbox4 {
  width: 30px;
  height: 30px;
  background: #c00;
}

.greenbox4 {
  width: 30px;
  height: 80px;
  background: #0c0;
}

.bluebox4 {
  width: 30px;
  height: 40px;
  background: #00f;
}

/*  */
.div5 {
  width: 100%;
  height: 150px;
  border: 1px solid #000;
  position: relative;
}

.redbox5,
.greenbox5,
.bluebox5 {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.redbox5 {
  width: 30px;
  height: 30px;
  background: #c00;
}

.greenbox5 {
  width: 30px;
  height: 80px;
  background: #0c0;
}

.bluebox5 {
  width: 30px;
  height: 40px;
  background: #00f;
}

/*  */
.div6 {
  width: 100%;
  height: 150px;
  border: 1px solid #000;
  display: flex;
  align-items: center;
  justify-content: center;
}

.redbox6 {
  width: 30px;
  height: 30px;
  background: #c00;
}

.greenbox6 {
  width: 30px;
  height: 80px;
  background: #0c0;
}

.bluebox6 {
  width: 30px;
  height: 40px;
  background: #00f;
}
</style>



